body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root, .wrapper{
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
background: pink;
}



.column{
  border-radius: 20px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  margin: 1%;
  width: 25%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.textCenter{
  text-align: center;
}

.blockElmCenter{
  margin: auto;
}
.content{
  height: 100%;
  /* width: inherit; */
  overflow-y: auto;
}

.item{
  padding: 3%;
  background: white;
  margin:3%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  max-height: 10%;
  overflow-y: auto;
  border-radius: 5px;
  border: solid;
}

.leftBtn , .rightBtn{
  width: 10%;
}
.item-content{
  width:80%;
  
  max-width: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  /* max-height: 10%; */
}


.headSection{
  height: 5%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: pink
}
.main-content{
  height: 95%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;

}

.addSection{
  margin: auto;
}
button{
  background: darkgrey;
  border: none;
  height: 30px;
  border-radius: 8px;
  outline: none;
  cursor: pointer;
}
.addSection button{
  width: 50px;
  height: 20px;
  border-radius: 2px;

}
input[type="text"]{
  outline: none;
  background: beige;
  border: none;
  width: 200px;
  border-radius: 10px;
  height: 20px;
  margin-left: 20px;
  margin-right: 20px;
  text-align: center;
}


.fade-in {
  animation: fadeIn ease 0.7s;
  -webkit-animation: fadeIn ease 0.7s;
  -moz-animation: fadeIn ease 0.7s;
  -o-animation: fadeIn ease 0.7s;
  -ms-animation: fadeIn ease 0.7s;
}


@keyframes fadeIn{
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}
